<template>
  <div class="barCharts">
    <div id="barCharts" ref="barCharts"></div>
  </div>
</template>

<script>
  import bus from '@/utils/bus'
  import * as echarts from 'echarts';
  //这是绘制图表的参数定义
  let optionTemp =
    {
      tooltip: {
        trigger: 'axis',
        backgroundColor: 'rgba(42, 92, 176, .6)',
        borderColor: "rgba(0, 255, 239, 1.0)",
        textStyle: {
          color: '#00FFEF'
        },
        axisPointer: {
          type: 'cross',
          crossStyle: {
            color: '#999'
          }
        }
      },
      grid: {
        left: '5',
        top: '30',
        right: '0',
        bottom: '0',
        containLabel: true
      },
      legend: {
        // show:false,
        data: ['legend1'],
        x:'center',      //可设定图例在左、右、居中
        y:'top',     //可设定图例在上、下、居中
        padding:[0,50,0,0],   //可设定图例[距上方距离，距右方距离，距下方距离，距左方距离
        textStyle: {color: '#fff'},
      },
      xAxis: [
        {
          type: 'category',
          data: [0],
          axisPointer: {
            type: 'shadow'
          },
          axisTick: { //x轴刻度线
            show: false,
          },
          axisLine: { //x轴
            show: false,
            // lineStyle:{
            //   color:'#00FF00',
            // }
          },
          axisLabel: { //设置x轴的字
            show: true,
            interval:0,//使x轴横坐标全部显示
            // rotate:50,//旋转
            textStyle: {//x轴字体样式
              color: "#333333",
              margin: 15,
              fontWeight: 'bold',
            }
          },
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '单位：xx',
          min: 0,
          max: 15,
          interval: 3,
          nameTextStyle:{
            color:"#66B9FF", 
        },
          axisLabel: { //设置y轴的字
            show: true,
            // formatter: '{value} ml',
            color:'#ff8000',
            interval: 0,//使y轴横坐标全部显示
            // formatter:'{value}%',
            textStyle: {//y轴字体样式
              color: "#333333",
              margin: 15,
            }
          },
          splitLine: {//网格线样式
            show: true,
            lineStyle:{
              color: '#0065b3',
              width: 1,
              type: 'solid'
            }
          },
          axisTick: { //y轴刻度线
            show: false,
          },
          axisLine: { //y轴
            show: false,
          },
        },
        // {
        //   type: 'value',
        //   name: '单位：%',
        //   min: 0,
        //   max: 100,
        //   interval: 20,
        //   left:'0',
        //   nameTextStyle:{
        //     color:"#66B9FF",
        // },
        //   axisLabel: { //设置y轴的字
        //     show: true,
        //     color:'#ff8000',
        //     interval: 0,//使y轴横坐标全部显示
        //     formatter:'{value}',
        //     textStyle: {//y轴字体样式
        //       color: "#66B9FF",
        //       margin: 15,
        //     }
        //   },
        //   splitLine: {//网格线样式
        //     show: true,
        //     lineStyle:{
        //       color: '#0065B3',
        //       width: 1,
        //       type: 'solid'
        //     }
        //   },
        // },
      ],
      series: [
        {
          name: 'legend1',
          type: 'bar',
          barWidth : 30,//柱图宽度
          data: [20,10,30,60,70,20,10,60,35,40,29,45],
          itemStyle: {
            normal: {
                label:{
                   show: true,
                   position: 'top',
                   textStyle: {
                   color: '#08a4fa'
                  }
                },
              color:new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#007EFF" // 0% 处的颜色
              }, {
                offset: 1,
                color: "#00FFEF" // 100% 处的颜色
              }], false)
            }
          },
        }
      ]
    }


  export default {
    name: "barCharts",
    props: {
      index:{
        type:Number,
        default:()=>{
          return null
        }
      },
      xData:{
        /*** * xData:['一月',二月]*/
        type:Array,
        default:()=>{
          return []
        }
      },
      seriesData: {
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示(单位)
      _zb: {
        /** :['单位','']*/
        type: Array,
        default: () => {
          return ['单位', '']
        },

        //echarts 样式
        legendStyle:{
          type:Object,
          default:()=>{
            return {color: '#333333'}
          }
        },
        xStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
              fontWeight: 'bold',
            }
          }
        },
        yStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
            }
          }
        }
      },

    },
    watch: {
      '$root.themeHomeChange'() {
        this.init();
      }
    },
    data() {
      return {
        option: optionTemp,
        mychart:null,
        time:null,
      }


    },
    mounted() {
      this.time = setInterval(()=>{
        this.mychart.resize()
      },300)
      this.init();
    },
    methods: {
      setTheme(){
        //样式区分
        if (this.$root.themeHomeChange==='1'){  //浅色背景
          this.option.legend.textStyle = {color: '#ffffff'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#ffffff",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].axisLabel.textStyle = {
            color: "#66B9FF",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].splitLine.lineStyle.color = '#ccc'
        }else if(this.$root.themeHomeChange==='2'){ //深色背景
          this.option.legend.textStyle = {color: '#333333'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].splitLine.lineStyle.color = '#0065b3'
        }
      },
      dataSet(){
        this.option.yAxis[0].name = this._zb[0]
        this.option.xAxis[0].name = this._zb[1]
        this.option.xAxis[0].data = this.xData
        if (this.seriesData.length>0){
          this.option.legend.data = []
          this.seriesData.forEach(item=>{
            this.option.legend.data.push(item.name)
          })
          this.option.series = this.seriesData
        }

        //样式
        this.setTheme()
      },
      init() {
        this.dataSet()
        this.mychart = echarts.init(this.$refs.barCharts, );
        //将会绘制图表的参数加到图表中

        this.mychart.setOption(this.option);
        let $this = this;
        this.mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
        });
        window.addEventListener("resize", ()=>{
          $this.mychart.resize()
        });
      },
    },
    beforeDestroy () {
      bus.$off('isCollapse');
      clearInterval(this.time);
    }

  }
</script>

<style scoped lang="scss">
.barCharts {
  height: 100%;
  #barCharts {
    height: 100%;
  }
}
</style>